import React, { Component } from 'react';
import '../App.css';
import request from 'superagent';

class Challenges extends Component{
    constructor(){
        super()
        this.state={
            challenges:[],
            star: '',
            showForm: false,
        }
        this.handleWeek = this.handleWeek.bind(this);
    }

    handleWeek(event){
        {this.state.showForm == false ? (
            (this.setState({showForm: true}))
        ): (this.setState({showForm: false}))}
    }

    componentDidMount(){
        const url='https://fortnite-public-api.theapinetwork.com/prod09/challenges/get?season=current';
        request
            .get(url)
            .set('Accept', 'application/json')
            .set('Access-Control-Allow-Origin', '*')
            .then(res => {
                this.setState({challenges: res.body['challenges'], star: res.body['star']})
            })
            .catch(err => alert(err))
    }

    RenderWeeks = () => {
        let result = []
        let arrweek = []
        let week = ""
        const  {challenges}  = this.state;
        const {star} = this.state;

        for (let i = 1; i < 11; i++) {
            week = "week" + i;
            console.log(week);
            arrweek = challenges[week] == null ? [] : challenges[week];
            result.push(
                <li style={{listStyle: 'none'}}>
                    <h3 style={{backgroundColor: "#F4F4F4", borderRadius: 17, paddingLeft: '5px'}}>Week {i}</h3>
                        <ul class="challenges">
                            {arrweek.map(c =>
                                <li name={c.challenge} class="challenge" style={{backgroundColor: "#F4F4F4", borderRadius: 17, fontSize: 20, left: 0}}>
                                    <span style={{width: 700}}>{c.challenge}</span>
                                    <span style={{width: 100, textAlign: 'center'}}>0/{c.total}</span>
                                    <div style={{width: '50px', textAlign: 'center'}}><img height="18.5px" width="18.5px" src={star}></img><span>{c.stars}</span></div>
                                </li>
                            )}
                        </ul>
                </li>
            )
        }
        return result
    }
    
    render(){
        const  {challenges}  = this.state;
        const {star} = this.state;
        const week1 = challenges['week1'] == null ? [] : challenges['week1'];
        console.log(challenges);
        return(
            <div>
                <h1>Challenges</h1>
                <ul class="week">
                    {/* <li>
                        <h3>Week1</h3>
                        <ul class="challenges">
                            {week1.map(c =>
                                <li name={c.challenge} class="challenge" style={{backgroundColor: "#F4F4F4", borderRadius: 17, fontSize: 20, left: 0}}>
                                    <span style={{width: 700}}>{c.challenge}</span>
                                    <span style={{width: 100}}>0/{c.total}</span>
                                    <div><img height="16.5px" width="16.5px" src={star}></img><span>{c.stars}</span></div>
                                </li>
                            )}
                        </ul>
                    </li> */}
                    {this.RenderWeeks()}
                </ul>
            </div>
        );
    }
}
export default(Challenges)
