import React, { Component } from 'react';
import {NavLink} from 'react-router-dom';

import StatsIcon from '@material-ui/icons/BarChart';
import ShopIcon from '@material-ui/icons/ShoppingCart';
import ChallengeIcon from '@material-ui/icons/Stars';

const Header = () => (
    <header style={{display: 'flex', paddingBottom: 35, textAlign: 'center'}}>
        <NavLink exact className="box_nav" activeClassName="active" to="/">
            <div style={{display: 'column'}}>
              <StatsIcon />
              <label>Stats</label>
            </div>
          </NavLink >
          <NavLink  className="box_nav" activeClassName="active" to="/shop">
            <div style={{display: 'column'}}>
              <ShopIcon />
              <label>Shop</label>
            </div>
          </NavLink >
          <NavLink  className="box_nav" activeClassName="active" to="/challenges">
            <div style={{display: 'column'}}>
              <ChallengeIcon />
              <label>Challenges</label>
            </div >
          </NavLink >
          <NavLink to="/">
            <img style={{height: 100, width: 'auto', position: 'absolute', right: 100}} src={require('../icon.png')} />
          </NavLink>
    </header> 
)
export default(Header)