import React, { Component } from 'react';
import '../App.css';
import request from 'superagent';

class Shop extends Component{ 
    constructor(){
        super()
        this.state={
            items:[]
        }
    }
    componentDidMount(){
        const url='https://fortnite-public-api.theapinetwork.com/prod09/store/get?language=de';
        request
            .get(url)
            .set('Accept', 'application/json')
            .set('Access-Control-Allow-Origin', '*')
            .then(res => 
                this.setState({items: res.body['items']})
            )
            .catch(err => alert(err))
    }

    render(){
        const { items } = this.state;
        console.log(items);
        return(       
            <div>
                <h1>Shop</h1>
                <ul style={{backgroundColor: "#F4F4F4", borderRadius: 17}} class="items">
                    {items.map(item =>
                        <li key={item.itemid} name={item.name} class="item">
                            <img height="220px" width="220px" src={item.item.images.information}></img>
                            {/* <span>{item.name}</span> */}
                        </li>
                    )}
                </ul>
            </div>
        );
    }    
}
export default(Shop)