import React, { Component } from 'react';
import '../App.css';
import Button from '@material-ui/core/Button';
import request from 'superagent';
import { white } from 'material-ui/styles/colors';

class Stats extends Component{
    constructor(){
        super()
        this.state={
            uid: '',
            name: '',
            data:[],
            
        }
        this.handleSearch = this.handleSearch.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleKeyPress = this.handleKeyPress.bind(this);
    }

    handleKeyPress(event) {
        if (event.key === 'Enter') {
          this.handleSearch(event);
        }
    }

    handleChange(event) {
        this.setState({name: event.target.value});
    }

    handleSearch(event){
        event.preventDefault();
        const name = this.state.name;
        console.log(name);
        if(name.value != ""){
            const url='https://fortnite-public-api.theapinetwork.com/prod09/users/id?username=' + name;
            request
                .get(url)
                .set('Accept', 'application/json')
                .set('Access-Control-Allow-Origin', '*')
                .then(res => {
                    this.setState({uid: res.body['uid']});
                    console.log(res.body['uid']);
                })
                .catch(err => alert(err))
        }
        setTimeout(() => {
            const uid = this.state.uid;
            if(uid.value != ""){
                const url='https://fortnite-public-api.theapinetwork.com/prod09/users/public/br_stats_v2?user_id=' + uid;
                request
                    .get(url)
                    .set('Accept', 'application/json')
                    .set('Access-Control-Allow-Origin', '*')
                    .then(res => {
                        this.setState({data: res.body['data']});
                        console.log(res.body['data']);
                    })
                    .catch(err => alert(err))
            }
        }, 2000);
    }

    renderData = () =>{
        const  {data}  = this.state;
        if(data.length != 0){
            const keyboardmouse = data['keyboardmouse'];
            const defaultsolo = keyboardmouse['defaultsolo'];
            const defaultduo = keyboardmouse['defaultduo'];
            const defaultsquad = keyboardmouse['defaultsquad'];
            const solo = defaultsolo['default'] == null ? [] : defaultsolo['default'];
            const duo = defaultduo['default'] == null ? [] : defaultduo['default'];
            const squad = defaultsquad['default'] == null ? [] : defaultsquad['default'];
            return(
                <div class="stats">
                    <div class="solo" style={{backgroundColor: "#F4F4F4", borderRadius: 17, fontSize: 20, left: 0}}>
                    <h2 style={{backgroundColor: "#c272d9", padding: '5px 10px', borderTopLeftRadius: 17, borderTopRightRadius: 17, color: 'white'}}>Solos</h2>
                        <p class="headline" style={{display: 'flex', justifyContent: 'space-around'}}><span>KILLS</span> <span>Matches Played</span> <span>Players outlived</span> <span>Minutes Played</span></p>
                        <p class="statsline" style={{display: 'flex', justifyContent: 'space-around'}}><span>{solo.kills}</span> <span>{solo.matchesplayed}</span> <span>{solo.playersoutlived}</span> <span>{solo.minutesplayed}</span></p>
                        <hr></hr>
                        <p class="headline" style={{display: 'flex', justifyContent: 'space-around'}}><span>Place Top 25</span> <span>Place Top 10</span> <span>Wins</span> <span>Score</span></p>
                        <p class="statsline" style={{display: 'flex', justifyContent: 'space-around', paddingBottom: '10px'}}><span>{solo.placetop25}</span> <span>{solo.placetop10}</span> <span>{solo.placetop1}</span> <span>{solo.score}</span></p>
                    </div>
                    <div class="duo" style={{backgroundColor: "#F4F4F4", borderRadius: 17, fontSize: 20, left: 0}}>
                    <h2 style={{backgroundColor: "#c272d9", padding: '5px 10px', borderTopLeftRadius: 17, borderTopRightRadius: 17, color: 'white'}}>Duos</h2>
                        <p class="headline" style={{display: 'flex', justifyContent: 'space-around'}}><span>KILLS</span> <span>Matches Played</span> <span>Players outlived</span> <span>Minutes Played</span></p>
                        <p class="statsline" style={{display: 'flex', justifyContent: 'space-around'}}><span>{duo.kills}</span> <span>{duo.matchesplayed}</span> <span>{duo.playersoutlived}</span> <span>{duo.minutesplayed}</span></p>
                        <hr></hr>
                        <p class="headline" style={{display: 'flex', justifyContent: 'space-around'}}><span>Place Top 12</span> <span>Place Top 5</span> <span>Wins</span> <span>Score</span></p>
                        <p class="statsline" style={{display: 'flex', justifyContent: 'space-around', paddingBottom: '10px'}}><span>{duo.placetop12}</span> <span>{duo.placetop5}</span> <span>{duo.placetop1}</span><span>{duo.score}</span></p>
                    </div>
                    <div class="squad" style={{backgroundColor: "#F4F4F4", borderRadius: 17, fontSize: 20, left: 0}}>
                    <h2 style={{backgroundColor: "#c272d9", padding: '5px 10px', borderTopLeftRadius: 17, borderTopRightRadius: 17, color: 'white'}}>Squads</h2>
                        <p class="headline" style={{display: 'flex', justifyContent: 'space-around'}}><span>KILLS</span> <span>Matches Played</span> <span>Players outlived</span> <span>Minutes Played</span></p>
                        <p class="statsline" style={{display: 'flex', justifyContent: 'space-around'}}><span>{squad.kills}</span> <span>{squad.matchesplayed}</span> <span>{squad.playersoutlived}</span> <span>{squad.minutesplayed}</span></p>
                        <hr></hr>
                        <p class="headline" style={{display: 'flex', justifyContent: 'space-around'}}><span>Place Top 6</span> <span>Place Top 3</span> <span>Wins</span> <span>Score</span></p>
                        <p class="statsline"style={{display: 'flex', justifyContent: 'space-around', paddingBottom: '10px'}}><span>{squad.placetop6}</span> <span>{squad.placetop3}</span> <span>{squad.placetop1}</span> <span>{squad.score}</span></p>
                    </div>
                </div>
            )
        }
    }

    render(){
        const {data} = this.state
        return(
            <div>
                <h1>Stats</h1>
                <div>
                    <input type="text" value={this.state.name} style={{borderRadius: 6, width: '600px', padding: 10, textAlign: 'center'}} placeholder='In-Game-Name' onChange={this.handleChange} onKeyPress={this.handleKeyPress}/>
                    <Button style={{backgroundColor: '#c272d9', color: '#fff', marginLeft: 5, marginRight: 5, height: 42.4, width: 117.59}} onClick={this.handleSearch}>
                        Search
                    </Button>
                </div>
                <div>
                    {this.renderData()}
                </div>
            </div>
        );
    }
}
export default(Stats)